.BuildControls{
    width: 100%;
    background-color: orange;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 2px 1px bisque;
            box-shadow: 0 2px 1px bisque;
    margin: auto;
    padding: 10px 0;
}
.OrderButton {
    background-color: #DAD735;
    outline: none;
    cursor: pointer;
    border: 1px solid #960944;
    color: #960909;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    -webkit-box-shadow: 2px 2px 2px #960910;
            box-shadow: 2px 2px 2px #960910;
}

.OrderButton:hover, .OrderButton:active {
    background-color: #ebe2f3;
    border: 1px solid #966909;
    color: #966909;
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}



